import PhotoBox from "../../components/Photo_box/photo_box";
import './home.css';
import { useNavigate } from "react-router-dom";
import Button from "../../components/Button/button";
import userAvatar from '../../assets/images/userAvatar.png';

export default function Home () {
    let navigate = useNavigate();
    const routeChange = (path) => () => {
        navigate(path);
    }

    return (
        <>
            <div className="startScreen">
                <PhotoBox 
                    avatar={<img src={userAvatar} className="userAvatar" alt="userAvatar" />}
                    name="Yuliya Stasheuskaya"
                    userSummary="Programmer. Creative. Innovator"
                    description={"Web developer with 7 months of experience in designing websites. \n Recognized for leadership, collaborative abilities when working in team settings."}
                />
                <Button 
                    className='startButton' 
                    title='Know more'
                    onClickHandler={routeChange("/portfolio/inner")}
                />
            </div>
        </>
    )
}